@import url("https://fonts.googleapis.com/css?family=Montserrat:700|Open+Sans&display=swap");

body {
  font-size: 16px;
  background: white;
  font-family: "Open Sans", sans-serif;
  /* overflow-x: hidden; */
  z-index: 1;
}

.App {
  margin: 0 auto;
  max-width: 1080px;
}

/************************************* Navbar.js *****************************************************/
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 4rem;
}
.logo {
  font-size: 2em;
  font-weight: bold;
}
.logo img {
  width: 20%;
  padding-left: 3px;
}

nav {
  left: 60em;
  top: 5em;
  z-index: 100;
}
nav li {
  display: inline-block;
  cursor: pointer;
}
nav li .link {
  text-decoration: none;
  color: black;
  padding: 1em;
  opacity: 0.5;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}
li .link:hover {
  opacity: 1;
}
.active {
  color: black;
  opacity: 1;
}

/************************************* Header.js *****************************************************/
.App-header {
  position: relative;
  min-height: 100vh;
  color: black;
  /* top: 4em; */
}
.heading-background {
  position: fixed;
  opacity: 0.05;
  font-size: 21.5em;
  left: 0;
  z-index: -100;
  color: #9352ae;
}
.header-title {
  position: relative;
  font-family: "Montserrat", sans-serif;
  font-size: 2.5em;
  text-align: left;
  top: 2em;
}
.header-title-mid {
  color: #dadada;
  text-decoration: line-through;
}
.header-title button {
  background: #7dba46;
  opacity: 0.8;
  border: 0px;
  padding: 10px 30px;
  transition: all 0.2s ease-out;
  -webkit-transition: all 0.2s ease-out;
  -moz-transition: all 0.2s ease-out;
  -ms-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
  font-size: 24px;
}
.header-title button:hover {
  opacity: 1;
  background: white;
  box-shadow: 0 0 25px 5px #cdcdcd;
  transition: all 0.2s ease-out;
}
.header-title button a {
  font-family: "Open Sans", sans-serif;
  color: white !important;
  text-decoration: none;
}
.header-title button a:hover{
  color: #7dba46 !important;
}
.header-links {
  display: flex;
  justify-content: space-between;
  width: 86%;
}
/************************************* About.js *****************************************************/
.about {
  display: flex;
  justify-content: space-between;
  align-items: top;
}
.about-content h1 {
  font-family: "Montserrat", sans-serif;
  font-size: 5em;
  text-align: left;
}
.about-content p {
  font-size: 1.3em;
  text-align: left;
  width: 40em;
}
.about .img-wrapper {
  background-color: white;
  height: 300px;
  width: 300px;
  clip-path: circle(50% at 50% 50%);
  text-align: center;
}
.about img {
  width: 100%;
  height: 120%;
  opacity: 0.9;
  transition: all 0.4s ease;
}
.about img:hover {
  opacity: 1;
  transform: scale(1.15);
  transition: all 0.4s ease;
  padding-top: 15px;
}

/************************************* Work.js *****************************************************/
.heading {
  font-family: "Montserrat", sans-serif;
  font-size: 5em;
  margin: 3em 0 0.5em 0;
}
.work-content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 6em;
}
.project-img {
  width: 100%;
  height: 100%;  
  transition: all 0.4s ease;
}
.project-img:hover {
  transform: scale(1.1);
  transition: all 0.4s ease;
}

/************************************* Contact.js *****************************************************/
.contact-heading {
  font-family: "Montserrat", sans-serif;
  font-size: 5em;
  margin: 3em 0 0.5em 0;
}
.amazing-color {
  color: #9352ae;
  opacity: 1;
}
.contact {
  margin-bottom: 12rem;
}
.contact-content {
  margin: 5rem 0;
}
.contact .email {
  color: black !important;
  font-size: 2em;
}
.contact li {
  display: inline-block;
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 1em;
  margin-left: 0;
}
.contact li a {
  text-decoration: none;
  color: black;
}
.footer {
  top: 2em;
  padding: 1em;
}
.footer a {
  text-decoration: none;
  color: chocolate !important;
}

/* SMARTPHONES PORTRAIT */
@media only screen and (min-device-width: 320px) and (max-device-width: 576px) {
  body {
    font-size: 12px;
    padding: 30px;
  }
  .App-header {
    min-height: 40vh;
  }
  header h1 {
    font-size: 5em;
    padding: 1em;
    margin-right: 0em;
  }
  .heading-background {
    display: none;
  }
  nav {
    font-size: 2.5em;
    width: 1000px !important;
  }
  .header-title {
    font-size: 3em;
    text-align: left;
    top: 1em;
    left: 0em;
  }

  .about {
    flex-direction: column;
    align-items: center;
    top: -30em;
  }
  .about-content {
    left: 2em;
  }
  .about-content p {
    font-size: 3.5em;
    text-align: left;
    width: 900px;
  }

  .work {
    left: 2em;
    top: 2em;
    align-items: flex-start;
  }
  .work .work-content {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 5em;
  }
  .project {
    text-align: center;
  }
  .project img {
    width: 60em;
    height: 45em;
  }
  .project h1 {
    font-size: 3.5em;
  }
  .project span {
    font-size: 2.5em;
  }

  .contact {
    margin-bottom: 5em;
  }
  .contact h1 {
    font-size: 4em;
  }
  .contact .email {
    font-size: 3.5em;
  }

  .contact li {
    display: block;
    padding: 2em;
  }
  .contact li a {
    font-size: 2.5em;
  }
  .footer {
    top: 4em;
    padding: 1em;
    font-size: 2em;
  }
}

/* iPads (portrait and landscape) ----------- */
@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
  body {
    font-size: 10px;
  }
  header h1 {
    font-size: 5em;
    padding: 1em;
    margin-right: 0em;
  }

  nav {
    display: none;
  }
  .header-title {
    font-size: 3.6em;
    text-align: left;
    top: 1em;
    left: 1em;
  }
  .about {
    /* position: relative; */
    display: flex;
    top: -10em;
  }
  .about img {
    display: none;
  }
  .about-content {
    left: 6em;
  }
  .about-content p {
    font-size: 3.5em;
    text-align: left;
    width: 700px;
  }

  .work {
    left: 6em;
    top: 4em;
    align-items: flex-start;
  }
  .work .work-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 5em;
  }
  .project img {
    width: 30em;
    height: 30em;
  }

  .contact {
    top: 15em;
    left: 6em;
  }
  .contact h1 {
    font-size: 4em;
  }
  .contact .email {
    font-size: 3.5em;
  }

  .contact li {
    display: block;
    padding: 2em;
  }
  .contact li a {
    font-size: 2.5em;
  }
  .footer {
    top: 4em;
    padding: 1em;
    font-size: 2em;
  }
}

/* Large screens ----------- */
@media only screen and (min-width: 1824px) {
  /* Styles */

  body {
    font-size: 20px;
  }
  header h1 {
    font-size: 3em;
    margin-right: 25em;
  }

  nav {
    left: 60em;
    top: 12em;
    z-index: 100;
  }
  nav li {
    font-size: 3em;
  }
  .header-title {
    font-size: 5em;
    top: 1em;
    left: 1em;
  }
  .about {
    display: flex;
    top: -20em;
  }
  .about-content {
    left: 10em;
  }
  .about-content p {
    font-size: 3.5em;
    text-align: left;
    width: 25em;
  }
  .about img {
    margin-left: 1em;
  }

  .work {
    left: 10em;
    top: 4em;
    align-items: flex-start;
  }
  .work .work-content {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
  .project img {
    width: 32em;
    height: 32em;
  }

  .contact {
    top: 10em;
    left: 8em;
  }
  .contact h1 {
    font-size: 8em;
  }
  .contact .email {
    font-size: 5em;
  }

  .contact li a {
    font-size: 4em;
    padding: 1em;
  }
  .footer {
    top: 4em;
    padding: 1em;
    font-size: 4em;
  }
}
